  align-items: center;
  padding: 8px 16px;
  display: flex;
  position: absolute;
}

.line {
  width: 30px;
  height: 2px;
  z-index: 1;
  background: #fff;
  margin: 5px;
  transition: all .4s;
  display: block;
}

.hamburguer-menu {
  height: 100%;
  width: 100%;
  opacity: 0;
  z-index: 2;
  margin: 0;
  position: absolute;
}

.change .first-line {
  transform: rotate(45deg)translate(0);
}

.change .second-line {
  transform: rotate(-45deg)translate(8px, -8px);
}

.change ~ .menu-expanded {
  max-width: 400px;
  opacity: 1;
}

@media (max-width: 400px) {
  .menu-expanded {
    width: 100%;
  }
}

